:root {
    --bg-def: #b6f09c;
    --bg-grad: linear-gradient(135deg,#b6f09c 20%, #47bbba);
}

.App {
    height: 100vh;

    display: flex;
    flex-direction: column;
    align-items: center;
    
    font-size: calc(10px + 2vmin);
    color: white;
    text-align: center;
    background-color: var(--bg-def);
    background: var(--bg-grad)
}

.heading {
    margin: 20px 0;
}

.logo {
    width: 50%;
}

.description {
    margin: 0;
}

input {
    border: none;
    text-align: center;
    font-size: 20pt;
    border-radius: 5px;
    width: 100%;

    color: #333;
}

button {    
    transition: .2s;
    border: 1px #b6f09c solid;
    background-color: #fff2;
    color: #b6f09c;

    margin-top: 20px;
    padding: 10px 30px;
    border-radius: 10px;
    font-size: 20px;
}

button:hover {
    background-color: #b6f09c;
    color: #fff;
    text-shadow: 1px 1px 4px #0005;
}

button:active {
    background-color: #b6f09cee;
    transform: translateY(1px);
}


.container {
    margin: 10px 0;

    box-sizing: border-box;
    border-radius: 20px;
    width: 95vw;
}

.inputArea {
    padding: 0 30px 25px 30px;
}

.listArea {
    background-color: #fffc;
    color: #333;
    
    flex: 0 1 auto;
    overflow-y: auto;
}

.code {
    position: relative;
}

.code.copied::after {
    content: "Copied!";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 25%;
    right: 25%;
    
    border-radius: 5px;
    background-color: #8adba8;
    animation: .18s appear-anim;

    opacity: 0.92;
}

@keyframes appear-anim {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 0.92;
    }
}

/* ===== DESKTOP CHANGES =====*/

@media only screen and (min-width: 800px) {
    .logo {
        width: 10%;
    }
    .description {
        font-size: 30pt;
    }
    .container {
        width: 700px;
    }
    .inputArea {
        padding: 0 50px 25px 50px;
    }
    .App {
        justify-content: center;
    }
}
